/*헤더*/
.app_header{
  width: 100%;
  height: 10vw;
  padding: 5% 5% 0 5%;
  box-sizing: border-box;
  background-color: #FEF9A7;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.app_header>h1{
  margin: 0;
  font-size: 3vw;
  /* font-weight: lighter; */
  font-style: italic;
  color: black;
}
.app_header>nav{
  width: 40%;
  height: inherit;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 2vw;
  /* font-weight: lighter; */
  color: black;
  letter-spacing: 1px;
  cursor: pointer;
}
@media screen and (min-width:768px){
  .app_header{
    background-color: mediumslateblue;
    height: 5vw;
  }
}
section{
  width: 100%;
}
h2{
  display: none;
}
/*섹션1*/
.sec_intro{
  background-color: #FEF9A7;
  height: 50vw;
  padding: 15% 5% 0 5%;
  box-sizing: border-box;
}
.sec_intro>p{
  /* background-color: darksalmon; */
  margin: 0;
  width: 100%;
  height: 100%;
  line-height: 9vw;
  font-size: 4vw;
  /* font-weight: bolder; */
  color: black;
  letter-spacing: 8px;
}

/*섹션2*/
.sec_projects{
  background-color: #fff;
  height: 80vw;
  padding: 0 0.5%;
  box-sizing: border-box;
}
.sec_projects>h3{
  /* background-color: mediumslateblue; */
  margin: 0;
  width: 100%;
  height: 20%;
  padding: 8% 0 0 0;
  box-sizing: border-box;
  text-align: center;
  font-size: 3vw;
  font-weight: normal;
  letter-spacing: 1px;
}
.cover_projects{
  /* background-color: rosybrown; */
  margin: 10% 0 0 0;
  width: 100%;
  height: 40%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.cover_content{
  /* background-color: rgba(25, 25, 112, 0.533); */
  width: 30%;
  height: 100%;
  border: 1px solid #cccccc40;
  border-radius: 3px;
  box-sizing: border-box;
  cursor: pointer;
}
.cover_content>div:nth-child(1){
  /* background-color: #FEF9A7; */
  width: 100%;
  height: 60%;
}
.cover_content>div:nth-child(2){
  background-color: #fefaa763;
  width: 100%;
  height: 40%;
  text-align: center;
}
.logo_content01{
  background: url(../public/logo01.png) no-repeat center center;
}
.logo_content03{
  background: url(../public/logo03.png) no-repeat center center;
}


/*섹션3*/
.sec_about{
  background-color: aliceblue;
  height: 60vw;
}

/*섹션4*/
.sec_contact{
  background-color: olive;
  height: 60vw;
}

/*풋터*/
.app_footer{
  background-color: #FEF9A7;
  width: 100%;
  height: 5vw;
  text-align: center;
  font-size: 1.5vw;
  color: rgba(0, 0, 0, 0.5);
}
